import React, { FunctionComponent } from "react";
import { Link, useLocation } from "react-router-dom";
import styles from './profile-nav.module.css';
import { getCookie, deleteCookie} from "../../utils/utils";
import { useDispatch } from "../../services/types/hooks";
import Api from "../../utils/Api";
import { USER_LOGOUT } from "../../services/constants";



///–∫–æ–º–ø–æ–Ω–µ–Ω—Ç –º–µ–Ω—é –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ –≤ –ª–∏—á–Ω–æ–º –∫–∞–±–∏–Ω–µ—Ç–µ
export const ProfileNav:FunctionComponent<{text:string, navconteiner: string}> = (props) => {

  const location = useLocation();

  const dispatch = useDispatch();

  return (
    <div className={props.navconteiner}>
        <nav className={`${styles.navmenu} mr-15`}>
          <ul className={styles.list}>
            <li className={styles.listelement}>
              <Link className={ location.pathname === '/profile' ? `${styles.currentlinktext} text text_type_main-medium`: `${styles.linktext} text text_type_main-medium`} to='/profile' >–ü—Ä–æ—Ñ–∏–ª—å</Link>
            </li>
            <li className={styles.listelement}>
              <Link className={location.pathname === '/profile/orders' ? `${styles.currentlinktext} text text_type_main-medium`: `${styles.linktext} text text_type_main-medium`} to='/profile/orders'>–ò—Å—Ç–æ—Ä–∏—è –∑–∞–∫–∞–∑–æ–≤</Link>
            </li>
            <li className={styles.listelement} onClick={() => { const match = getCookie('refreshToken'); match && Api.logoutRequest(match); deleteCookie('token'); deleteCookie('refreshToken'); dispatch({type:USER_LOGOUT}) }}>
              <Link className={`${styles.linktext} text text_type_main-medium`} to='/profile'>–í—ã—Ö–æ–¥</Link>
            </li>
          </ul>
        </nav>
      <p className={`${styles.blockquote} text text_type_main-default text_color_inactive mt-20`}>{props.text}</p>

      </div>
  )

}
